

.landpage-main{
    background-position: center center;
    background-size: cover;
    height: 100vh !important;
    .overlay-div{
        background-color : rgba(242, 75, 75, 0.7);
        width: inherit;
        height: inherit;
    }
    h1,h2,h3,h4,h5{
        text-transform: capitalize;
    }
    button{
        background-color: $H-B-C2;
        border-color: $H-B-C2;
        border-radius: 2em;
        &:hover ,&:active{
            background-color: $H-B-C1 !important;
            border-color: $H-B-C1 !important;
        }
        &:focus,&:active{
              box-shadow:   0 0 0 0.2rem rgba(251, 255, 38, 0.5) !important;
        }
        a{
            &:hover{
                text-decoration: none;
            }
        }
    }
}