.background-badge {
  height: 100vh;
  background-color: rgba(242, 75, 75, 0.9);

  ul li {
    background-color: $FNT-WHITE;
    color: $H-B-C2;
    font-size: 1.25rem;
    padding: 0.4rem 0.7rem;
    border-radius: 2em;
  }
  p.lead {
    background-color: rgba(242, 183, 5, 0.9);
  }
}

.dispo {
  background-color: $FNT-WHITE;
  border-color: $FNT-WHITE;
  animation-name: CoppyAnimation;
  animation-duration: 2s;
  animation-timing-function: linear;
  animation-delay: 1s;
  position: absolute;
  bottom: 0;
  right:2em;
  font-weight: bold;
  z-index: 8888;
}
@keyframes CoppyAnimation {
  0% {
    bottom: 0;
  }
  100% {
    bottom: 100%;
  }
}
